@import url(https://fonts.googleapis.com/css2?family=Concert+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: "Concert One", cursive;
  background-color: #ffbe0b;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
}

#pomodoro-h1 {
  font-size: 75px;
  text-align: center;
  margin-bottom: 100px;
  margin-top: 100px;
}

.github-link {
  background-image: url("https://assets.stickpng.com/images/5847f98fcef1014c0b5e48c0.png");
  width: 50px;
  height: 50px;
  display: inline-block;
  background-size: 100%;
  -webkit-filter: invert(13%) sepia(78%) saturate(6194%) hue-rotate(257deg)
    brightness(83%) contrast(116%);
          filter: invert(13%) sepia(78%) saturate(6194%) hue-rotate(257deg)
    brightness(83%) contrast(116%);
}
.github-link:hover {
  -webkit-filter: none;
          filter: none;
}

@media only screen and (max-width: 768px) {
  #pomodoro-h1 {
    font-size: 50px;
    margin-top: 50px;
    margin-bottom: 50px;
  }
}

@-webkit-keyframes minusecs-animation {
  0% {
    color: black;
  }

  50% {
    color: rgb(255, 115, 0);
  }
  100% {
    color: black;
  }
}

@keyframes minusecs-animation {
  0% {
    color: black;
  }

  50% {
    color: rgb(255, 115, 0);
  }
  100% {
    color: black;
  }
}

@-webkit-keyframes start-stop-animation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

@keyframes start-stop-animation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}
.clock-container {
  border-radius: 10px;
  width: 500px;
  height: 550px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition-duration: 0.2s;
  background-color: #f1b203;
  box-shadow: -7px 7px 1px 1px #b98a07;
}

.clock-timer-container {
  width: 400px;
  height: 250px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
#timer-label {
  font-size: 50px;
  margin-bottom: 50px;
}
#time-left {
  font-size: 90px;
}
.time-left-minusecs {
  display: inline-block;
  width: 150px;
  cursor: pointer;
  font-family: "Orbitron", sans-serif;
  background-color: white;
  padding: 15px 10px 15px 10px;
  border-radius: 10px;
  box-shadow: -5px 5px 1px 1px rgb(114, 114, 114);
}

.time-left-minusecs:hover {
  box-shadow: -5px 5px 1px 1px rgb(75, 74, 74);
  -webkit-animation-name: minusecs-animation;
          animation-name: minusecs-animation;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

#minusecs-between {
  margin-left: 15px;
  margin-right: 15px;
}

.clock-icon-start-stop {
  font-size: 50px;
  margin-top: 50px;
  margin-bottom: 50px;
  transition-duration: 0.1s;
  cursor: pointer;
  color: white;
  text-shadow: -2px 4px rgb(102, 101, 101);
}
.clock-icon-start-stop:hover {
  color: rgb(201, 201, 201);
  text-shadow: -1px 3px rgb(114, 113, 113);
  -webkit-animation-name: start-stop-animation;
          animation-name: start-stop-animation;
  -webkit-animation-duration: 0.9s;
          animation-duration: 0.9s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.clock-icon-start-stop:active {
  color: black;
  text-shadow: 0px 0px rgb(221, 220, 220);
  transform: translateY(1px);
}
#start_stop {
  margin-right: 30px;
}
#reset {
  margin-left: 30px;
}

.clock-row-break-session {
  display: inline-flex;
  flex-direction: row;
  font-size: 30px;
}
.break-session-settings {
  display: flex;
  flex-direction: column;
}
#session-container {
  margin-right: 40px;
}
#session-length {
  margin: 0px 10px 0px 10px;
}
#break-length {
  margin: 0px 10px 0px 10px;
}

.clock-icon-plus-minus {
  color: white;
  transition-duration: 0.1s;
  cursor: pointer;
  text-shadow: -2px 3px rgb(102, 101, 101);
}
.clock-icon-plus-minus:hover {
  color: rgb(201, 201, 201);
  text-shadow: -1px 3px rgb(114, 113, 113);
}
.clock-icon-plus-minus:active {
  color: black;
  text-shadow: 0px 0px rgb(221, 220, 220);
  transform: translateY(1px);
}

.break-session-label {
  margin-bottom: 15px;
}

@media only screen and (max-width: 768px) {
  .clock-container {
    width: 370px;
    height: 520px;
    transform: scale(0.95, 0.95);
  }
  #time-left {
    font-size: 70px;
  }
  .clock-icon-start-stop {
    margin-bottom: 30px;
  }
  .time-left-minusecs {
    width: 130px;
  }
}

